<template>
    <div class="container">
        <div class="row align-items-center min-vh-100">
            <div class="col-12 col-sm-8 col-md-6 col-lg-4 offset-0 offset-sm-2 offset-md-3 offset-lg-4">
                <form @submit="loginWithEmailAndPassword">
                <div class="mb-3 text-center">
                    <h2>제주 여행</h2>
                </div>
                <hr />
                <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="!!this.message" >
                    {{ this.message }}
                </div>
                <div class="mb-3">
                    <label class="form-label" for="email">이메일</label>
                    <input
                    class="form-control"
                    type="email"
                    name="email"
                    id="email"
                    v-model="this.email"
                    placeholder="e-메일계정"
                    required
                    />
                </div>
                <div class="mb-4">
                    <label class="form-label" for="password">암호</label>
                    <div class="input-group mb-3">
                    <input
                        :type="this.isPasswordVisible ? 'text' : 'password'"
                        class="form-control"
                        name="password"
                        placeholder="암호"
                        v-model="this.password"
                        required
                    />
                    <div class="input-group-text">
                        <input
                        class="form-check-input mt-0 me-2"
                        type="checkbox"
                        aria-label="Checkbox for following text input"
                        v-model="this.isPasswordVisible"
                        />Show
                    </div>
                    </div>
                </div>            
                <div class="d-flex justify-content-center align-items-center">
                    <button class="btn btn-primary">로그인</button>
                    <span class="ms-3 text-end">등록을 안 하셨다면? <router-link to="/sign-up">등록</router-link></span>
                    <span class="ms-3 text-start"><router-link to="/passwd">암호재설정</router-link></span>                
                </div>
                <div class="footer">만든이 : 유용규</div>
                </form>

                <div class="text-center my-3">
                    <h4 class="mb-3">또는</h4>
                    <button
                        class="btn btn-lg btn-outline-primary"
                        @click="this.$store.dispatch('signInWithGoogle')"
                    >
                        <div class="d-flex justify-content-center align-items-center gap-2">
                        <span class="align-self-baseline"> 구글 로그인 </span>
                        </div>
                    </button>
                </div>
                
                <div class="mb-3 text-center">
                    <a id="custom-login-btn" @click="kakaoLogin()">
                        <img
                            src="https://k.kakaocdn.net/14/dn/btroDszwNrM/I6efHub1SN5KCJqLm1Ovx1/o.jpg"
                            width="222"
                            alt="카카오 로그인 버튼"
                        />
                    </a>
                </div>                

                <div class="mb-3 text-center">
                    <a @click="loginWithNaver">
                        <img src="https://static.nid.naver.com/oauth/big_g.PNG" width="222">
                    </a>
                </div>
                
                <!-- <div class="mb-3 text-center">
                    <button class="btn btn-facebook" @click="facebookLogin">
                        <i class="fa fa-facebook mr-1"></i>
                        페이스북 로그인
                    </button>
                </div> -->

            </div>
        </div>        
    </div>    
</template>

<script>

export default {
    name: "SignInView",    
    data() {
        return {
        email: "",
        password: "",
        username:"",
        message: "",
        isPasswordVisible: false,
        naverLogin: null,        
        };
    },

    mounted() {     

    },
    
    methods: {

        async loginWithEmailAndPassword(event) {
            event.preventDefault();
            
            const formData = {
                email: this.email,
                password: this.password,                
            };

            try {
                await this.$store.dispatch("signInWithEmailAndPassword", formData);
            } catch (error) {
                this.message = error.message;
            }
        },

        kakaoLogin() {                
            window.Kakao.Auth.login({
                scope: "profile_image, account_email",
                success: this.getKakaoAccount,
            });
        },
                
        getKakaoAccount() {
            window.Kakao.API.request({
                url: "/v2/user/me",
                success: (res) => {
                    const kakao_account = res.kakao_account;
                    const ninkname = kakao_account.profile.ninkname;
                    const email = kakao_account.email;
                    console.log("ninkname", ninkname);
                    console.log("email", email);
                    //로그인처리구현
                    console.log("로그인 성공!");
                    this.$store.dispatch("signInWithKakao", {
                        username:ninkname,
                        email: email,
                    });

                },
                fail: (error) => {
                    console.log(error);
                },
            });
        },

        loginWithNaver() {

            var mycallback = "http://" + window.location.hostname + ((location.port==""||location.port==undefined)?"":":" + location.port) + "/navercallback";
            const naverLogin = new naver.LoginWithNaverId({
                clientId: 'kRURwahAXMuelx8fF5Vw',
                callbackUrl: mycallback,
                callbackHandle: true
            })
            naverLogin.init();
            naverLogin.reprompt();
        },

    },
};
</script>

<style scoped>
.footer {
    padding: 20px;
    text-align: right;
}
</style>
